@import './variables';

:root {
	--primary: #fff;
	--bg-primary: #000202;
	--dark-primary: #fff;
	--dark-heading: #bbb;
}

* {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	box-sizing: border-box;
	appearance: none;
	-moz-appearance: none;
	-webkit-appearance: none;
	padding: 0;
	margin: 0;
	cursor: none;
}

body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	overflow-x: hidden;
	background: var(--bg-primary);
	font-family: monospace;
	line-height: 1.5;
	color: var(--dark-primary);
	position: relative;
}

button,
input {
	outline: none;
}

a {
	text-decoration: none;
}

ul {
	padding: 0;
	list-style-type: none;
}

.container {
	width: 100%;
	max-width: 1480px;
	transition: max-width 0.3s ease;
	position: relative;
	padding: 0 40px;
	margin-left: auto;
	margin-right: auto;
	&.container__fullHeight {
		height: 100%;
	}
}

.word.special_word {
	-webkit-text-fill-color: #fff;
	-webkit-text-stroke-color: #f00;
	opacity: 0;
}
