.wrapper {
	padding-top: 0;
	padding-bottom: 0;
}
.inner {
}
.intro {
	position: relative;
	height: 100vh;
	.bg_image {
		background: url(https://arlo.frenify.net/demos/personal4/wp-content/uploads/2020/06/5-3.jpg);
		position: absolute;
		top: 0px;
		bottom: 0px;
		left: 0px;
		right: 0px;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}
}

.content {
	position: relative;
	max-width: 800px;
	z-index: 2;
	top: 50%;
	transform: translateY(-50%);
	margin-left: auto;
	.title {
		margin-bottom: 41px;
	}
	h3 {
		color: var(--dark-heading);
		font-size: 72px;
		font-weight: 400;
		line-height: 80px;
		letter-spacing: 0.5px;
		margin: 0;
	}
}
.icon_box {
	display: inline-block;
	width: 60px;
	height: 60px;
	border-radius: 100%;
	background-color: var(--bg-primary);
	box-shadow: 0px 3px 10px #f9004d;
	position: relative;
	margin-bottom: 46px;
	svg {
		width: 20px;
		height: 20px;
		fill: currentcolor;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #bbb;
	}
}

.gamma_social {
	ul {
		margin: 0px;
		li {
			margin: 0px 40px 0px 0px;
			display: inline-block;
			position: relative;
			&::before {
				position: absolute;
				content: '';
				width: 20px;
				height: 1px;
				background-color: #666;
				top: 50%;
				transform: translateY(-50%);
				left: 100%;
				margin-top: 1px;
				margin-left: 10px;
			}
			a {
				color: #666;
				font-size: 1.5rem;
				font-weight: 400;
				line-height: 1.5em;
				letter-spacing: 0.5px;
				position: relative;
				transform: translateZ(0);
				transition: all 0.3s ease;
				&::before {
					background-color: #666;
					content: '';
					width: 0;
					height: 1px;
					position: absolute;
					bottom: -2px;
					left: 100%;
					-webkit-transition: left 0.5s ease, width 0.5s ease;
					-moz-transition: left 0.5s ease, width 0.5s ease;
					-ms-transition: left 0.5s ease, width 0.5s ease;
					-o-transition: left 0.5s ease, width 0.5s ease;
					transition: left 0.5s ease, width 0.5s ease;
				}
				&:hover {
					color: #fff;
					&::before {
						background-color: #fff;
						width: 100%;
						left: 0;
						transition: width 0.5s ease;
					}
				}
			}
		}
		li:last-child {
			margin-right: 0px;
			&::before {
				content: none;
			}
		}
	}
}
