.wrapper {
	--min-height: 100px;
	height: var(--min-height);
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	padding: 0 50px;
	background: transparent;
	z-index: 999;
}
.header-bar {
	width: 100%;
	display: flex;
	flex-direction: row;
	min-height: var(--min-height);
	justify-content: space-between;
	align-items: center;
	position: relative;
}

.logo {
	margin-right: 30px;
	margin-left: 0;
	a {
		display: block;
	}
	img {
		user-select: none;
		-webkit-user-select: none;
		-ms-user-select: none;
		-webkit-touch-callout: none;
		-o-user-select: none;
		-moz-user-select: none;
		max-width: 250px;
		max-height: 90px;
	}
}

.menu {
	margin-left: 30px;
}
.menu-icon {
	margin-left: 0;
	margin-right: 0;
	cursor: pointer;
	svg {
		height: 30px;
		color: var(--dark-primary);
	}
}
